
table {
  width: 90%;
  margin: 20px auto;
  border-collapse: collapse;
  background-color: #fff;
  color: #333;
}

th, td {
  padding:8px;
  border: 1px solid #ddd;
  text-align: left;
}

th {
  padding: 4px;
  font-size: 0.6rem;
  text-align: center;
  background-color:rgba(108, 157, 236, 0.5);
  color: white;
}

tr{
  font-size: 0.5rem;
}